import React, { Component } from 'react'
import styled from 'styled-components'
import { StaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'

const StyledImageWrapper = styled.div`
  box-shadow: 16px 16px 0 0 ${({ theme }) => theme.colors.semiTransparentGrey};
  margin: 0 1rem 1rem 0;
  position: relative;
`
const StyledItemWrapper = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: ${({ theme }) => theme.colors.semiTransparentBlack};
  display: grid;
  place-items: center;
  opacity: ${({ active }) => (active ? 1 : 0)};
`
const StyledContentWrapper = styled.div`
  /* background-color: ${({ theme }) => theme.colors.lightgrey}; */
`

const StyledLink = styled.a`
  display: block;
  padding: 0 2rem;
  ${({ theme }) => theme.font('montserrat', '600')}
  line-height: 2em;
  background-color: greenyellow;
`

const GithubIcon = () => (
  <StaticQuery
    query={graphql`
      query {
        github: file(relativePath: { eq: "github.png" }) {
          childImageSharp {
            fluid(maxWidth: 128) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => <Img fluid={data.github.childImageSharp.fluid} />}
  />
)

class ItemWrapper extends Component {
  state = { active: true }
  render() {
    const { active } = this.state
    const { codeUrl, demoUrl } = this.props
    return (
      <StyledItemWrapper active={active}>
        <StyledContentWrapper>
          <GithubIcon />
          <StyledLink href={codeUrl} target="_blank">
            Code
          </StyledLink>
          <StyledLink href={demoUrl} target="_blank">
            Demo
          </StyledLink>
        </StyledContentWrapper>
      </StyledItemWrapper>
    )
  }
}

const ImageWrapper = ({ children, className, codeUrl, demoUrl }) => {
  return (
    <StyledImageWrapper className={className}>
      {children}
      <ItemWrapper codeUrl={codeUrl} demoUrl={demoUrl} />
    </StyledImageWrapper>
  )
}

export default ImageWrapper
